*,
*::before,
*::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

:root {
  --gold: hsl(24, 49%, 84%);
  --black: hsl(0, 0%, 8%);
  --gray: hsl(0, 0%, 27%);
  --white: hsl(0, 0%, 100%);
}

:root {
  --font-shoulder: "Big Shoulders Display", cursive;
  --font-outfit: "Outfit", sans-serif;
}

html {
  font-size: 62.5%;
}

body {
  font-size: 1.8rem;
  font-family: var(--font-outfit);
}

.heading-primary {
  font-family: var(--font-shoulder);
  font-size: 6rem;
  line-height: 0.917;
  text-transform: uppercase;
}

.hero {
  margin-bottom: 12rem;
}
.hero__title {
  margin-bottom: 3.2rem;
}
.hero__img {
  margin-bottom: 3.2rem;
}
.hero__container {
  padding: 0 1.6rem;
}
.hero__description {
  color: var(--gray);
  margin-bottom: 3.2rem;
}